@import 'smart-grid', 'font';
*{margin: 0;padding: 0;box-sizing: border-box;}
body {
	font-size: 14px;
	font-family: 'Roboto-Thin', sans-serif;
}
img {max-width:100%;}
.container {
	@include wrapper();
    position: relative;
}
.row {
	@include row-flex();
	justify-content: space-between;
	align-items: center;
}
h2.h2__slog {
	color: #3d3d3d;
	font-family: "Titling";
	font-size: 48px;
	font-weight: 400;
	text-transform: uppercase;
	letter-spacing: 4.8px;
	text-align: center;
	display: block;
	position: relative;
	margin-left: -25px;
	&::after {
		content: '';
	    background-color: #f99c7e;
	    width: 153px;
	    height: 2px;
	    position: absolute;
	    bottom: 5px;
	    right: 11px;
	}
}

p.p__title {
	word-wrap: break-word;
    line-height: 16px;
    color: #696969;
    font-family: "Roboto-Thin";
