		color: #fff;
	    font-family: "Roboto";
	    font-size: 24px;
	    font-weight: 600;
	    text-transform: uppercase;
	    background-color: transparent;
	    border: none;
	    cursor: pointer;
	    padding-left: 1px;
	    letter-spacing: 0.3px;
	    position: relative;
	    &:after {
			content: '';
		    position: absolute;
		    right: -30px;
		    top: 10px;
		    width: 18px;
		    height: 20px;
		    background-repeat: no-repeat;
		    background-image: url(../img/control/arowDown.png);
